@font-face {
    font-family: "Chilanka-Regular";
    src: url("fonts/Chilanka-Regular.ttf");
}
@font-face {
    font-family: "CraftyGirls-Regular";
    src: url("fonts/CraftyGirls-Regular.ttf");
}
@font-face {
    font-family: "Pangolin-Regular";
    src: url("fonts/Pangolin-Regular.ttf");
}

/* All pages - Generic CSS */
body {
    background-color: #f5f5f4;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

header {
    width: 80vw;
    box-sizing: border-box;
    align-self: center;
}

header h1 {
    font-family: CraftyGirls-Regular ;
    text-align: center;
    color: #388087;
    margin-top: 60px;
}

header h2 {
    font-family: Chilanka-Regular ;
    color: #81b9af;
}

nav {
    background-color: #daefe0;
    top: 0px;
    width: 80vw;
    box-sizing: border-box;
    border: 2px #7cc3ca solid;
    position: fixed;
}

nav ul {
    display: flex;
    justify-content: space-around;
}

nav li{
    font-family: Pangolin-Regular ;
    font-weight: bold;
    font-size: large;
    list-style-type: none;
}

nav li a {
    text-decoration:none;
    color: #388087;
}

li.active a{
    background-color :#7cc3ca;
    color: white;
}

main {
    background-color: #cdeed740;
    padding: 30px;
    font-family: Chilanka-Regular ;
    color: #2c9284;
    border: 1px #7cc3ca solid;
    width: 80vw;
    box-sizing: border-box;
}

main p {
    font-style: italic;
    text-align: justify;
}

footer{
    width: 80vw;
    box-sizing: border-box;
    background-color: #388087;
    color: #ffffff;
    margin: 10px 0 0 0;
    padding: 10px;
    text-align: center;
    font-family: Pangolin-Regular ;
}

figure {    
    background-color: #ffffff;
    border: 2px #7cc3ca solid;
}

figcaption {
    color: #000000;
    text-align: center;
}

img {
    width: 100%;
}

.leftfloating{
    float:left;
}

.rightfloating{
    float:right;
}

table {
    background: #ffffff;
    border: 2px #7cc3ca solid; 
    border-collapse: collapse;
}

th, td {
    border: 1px #7cc3ca solid; 
    padding: 8px;
}

th {
    text-align: center;
}

tr:nth-child(even) {
    background-color: #f9ffc7;
}

/* Login page */

/* CSS for svg elemnets on login page */
.checkResponse {
    color : red;
    padding-left: 1em;
}

#loginPageSVG, #loginPageSVG2 {
    background-color: white;
    border: 2px solid #7cc3ca;
    margin: 20px 0 0 0;
    width: 70vw;
    height: 30rem;
}

.formatting {
    border-top: 2px dotted #7cc3ca;
    margin: 20px 0 0 0;
    padding: 10px 0 0 10px;
}

.formatting h2 {
    color: #388087;
    text-align: center;
}

/*  Application page CSS */

/* CSS for form */

main#applicationForm input:valid {
    background-color: #d4eabf;
}
main#applicationForm input:invalid {
    background-color: #ffcbcb;
}

#applicationForm {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.applicationForm {
    display: grid;
    grid-template-columns: 10em 20rem;
    grid-row-gap: 1em;
    grid-column-gap: 2em;
    padding: 1em;
    border: 2px dashed #7cc3ca;
}

.applicationForm label {
    font-family: Chilanka-Regular;
    justify-self: end;
}

/* Thank you section css */
.hiddenSection {
    display:none;
}

.showSection {
    display:block;
}

#ThanksDialog {
    background-color: #3f6365b3;
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0px;
    left:0px;
}

#Dialogdiv {
    background-color: #f9ffc7;
    color: #2c9284;
    margin-top:15%;
    margin-left: 30%;
    width: 30%;
    border: 2px solid #7cc3ca;
    border-radius: 1em;
    text-align: center;
    padding: 20px;
}

/* Admiin Activities Page */

#addNewEvent input:valid {
    background-color: #d4eabf;
}
#addNewEvent input:invalid {
    background-color: #ffcbcb;
}

#addNewEvent {
    display: grid;
    grid-template-columns: 10em 20rem;
    grid-row-gap: 1em;
    grid-column-gap: 1em;
    border: 2px dashed #7cc3ca;
    padding: 1em;
    margin-bottom: 2em;
}

#addNewEvent label {
    justify-self: end;
}